@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: boder-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: #0c0c30;
  --color-bg-variant: #2c2c6c;
  --color-primary: #4db5ff;
  --color-primary-variant: rgba(77, 181, 255, 0.4);
  --color-white: #fff;
  --color-light: rgba(255, 255, 255, 0.6);

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: "Poppins", sans-serif;
  background: #0c0c30;
  background: var(--color-bg);
  color: #fff;
  color: var(--color-white);
}

/* General Class */
.container {
  width: 75%;
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-size: 3rem;
}

section {
  margin-top: 8rem;
}

section > h2,
section > h5 {
  text-align: center;
  color: rgba(255, 255, 255, 0.6);
  color: var(--color-light);
}

section > h2 {
  color: #4db5ff;
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-alight {
  color: rgba(255, 255, 255, 0.6);
  color: var(--color-light);
}

a {
  color: #4db5ff;
  color: var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition);
}

a:hover {
  color: #fff;
  color: var(--color-white);
}

.btn {
  width: -webkit-max-content;
  width: max-content;
  display: inline-block;
  color: #4db5ff;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid #4db5ff;
  border: 1px solid var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition);
}

.btn:hover {
  background: #fff;
  background: var(--color-white);
  color: #0c0c30;
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: #4db5ff;
  background: var(--color-primary);
  color: #0c0c30;
  color: var(--color-bg);
}

.img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* Media Queries (Medium Device) */

@media screen and (max-width: 1024px) {
  .container {
    width: 86%;
    width: var(--container-width-md);
  }
  section {
    margin-top: 6rem;
  }
}

/* Media Queries (Small Devise) */

@media screen and (max-width: 600px) {
  .container {
    width: 90%;
    width: var(--container-width-sm);
  }
  section > h2 {
    margin-bottom: 2rem;
  }
}

header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}

.header_container {
  text-align: center;
  display: block;
}

/* CTA */

.cta {
  margin-top: 2rem;
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  justify-content: center;
}

/* Header Social */

.header_social {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 0.8rem;
  gap: 0.8rem;
  position: absolute;
  left: 3;
  bottom: 2.5rem;
}

.header_social::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}

/* Picture */
.me {
  background: linear-gradient(
    180deg,
    var(--color-primary),
    var(--color-bg-variant),
    transparent
  );
  width: 22rem;
  height: 30rem;
  position: absolute;
  display: block;
  left: calc(50% - 11rem);
  margin-top: 4rem;
  padding: 0;
  border-radius: 12rem 12rem 12rem 12rem;
  overflow: hidden;
}

.profil {
  width: 100%;
  margin-top: -1rem;
  bottom: 0;
  border: 2px solid var(--color-bg);
  border-radius: 22rem;
}

/* Scroll Down */

.scroll_down {
  position: absolute;
  right: 5rem;
  bottom: 6rem;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  font-weight: 300px;
  font-size: 0.9rem;
}

/* Medium Device */
@media screen and (max-width: 1024px) {
  header {
    height: 100vh;
  }
}

/* Small Device */
@media screen and (max-width: 600px) {
  header {
    height: 90vh;
  }
  .me {
    width: var(--container-sm);
    height: 20rem;
    margin: 1rem 3.5rem;
  }
  .header_social,
  .scroll_down {
    display: none;
  }
}

nav {
  background: rgba(0, 0, 0, 0.3);
  width: -webkit-max-content;
  width: max-content;
  padding: 0.7rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  bottom: 2rem;
  display: flex;
  grid-gap: 0.8rem;
  gap: 0.8rem;
  border-radius: 3rem;
  -webkit-backdrop-filter: blur(15px);
          backdrop-filter: blur(15px);
  margin: auto;
}

nav a {
  background: transparent;
  padding: 1rem;
  border-radius: 50%;
  display: flex;
  color: var(--color-light);
  font-size: 1.1rem;
}

nav a:hover {
  background: rgba(0, 0, 0, 0.3);
}

.about_container {
  display: grid;
  grid-template-columns: 30% 50%;
  grid-gap: 15%;
  gap: 15%;
}

.about_me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(45deg, transparent, #b0b8b8, transparent);
  display: grid;
  place-items: center;
}

.about_img {
  width: 100%;
  border-radius: 2rem;
  overflow: hidden;
  -webkit-transform: rotate(10deg);
          transform: rotate(10deg);
  transition: var(--transition);
}

.about_img:hover {
  -webkit-transform: rotate(0);
          transform: rotate(0);
}

.about_cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.about_card {
  background: var(--color-bg-variant);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 1rem;
  text-align: center;
}

.about_card:hover {
  background: transparent;
  border-color: var(--color-bg-variant);
  cursor: default;
}

.about_icon {
  color: var(--color-primary);
  font-size: 1.4rem;
  margin-bottom: 1rem;
}
.about_card h5 {
  font-size: 0.95rem;
}

.about_card small {
  color: var(--color-light);
}

.about_content p {
  padding: 2rem 0 2.6rem;
  color: var(--color-light);
}

/* Medium Device */
@media screen and (max-width: 1024px) {
  .about_container {
    grid-template-columns: 1fr;
    grid-gap: 0rem;
    gap: 0rem;
  }
  .about_me {
    width: 50%;
    margin: 2rem auto 3rem;
  }
  .about_content p {
    padding: 1rem 0 1.5rem;
  }
}

@media screen and (max-width: 600px) {
  .about_me {
    width: 65%;
    margin: 0 auto 3rem;
  }

  .about_cards {
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
    gap: 1rem;
  }

  .about_content {
    text-align: center;
  }

  .about_content p {
    margin: 1.5rem 0;
  }
}

.service_container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 3rem;
  gap: 3rem;
}

.services {
  background: var(--color-bg-variant);
  border: 1px solid var(--color-primary);
  border-radius: 0 0 2rem 2rem;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  transition: var(--transition);
}
.service:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}

.service_head {
  background: var(--color-primary);
  padding: 2rem;
  border-radius: 0 0 2rem 2rem;
  box-shadow: 0 2rem 1rem rgba(0, 0, 0, 0.2);
}

.service_head h3 {
  color: var(--color-bg);
  font-size: 1rem;
  text-align: center;
}

.service_list {
  padding: 2rem;
}

.service_list li {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 0.8rem;
}

.service_list-icon {
  color: var(--color-primary);
  margin-top: 2px;
}

.service_list p {
  font-size: 0.9rem;
}

/* Medium Device */
@media screen and (max-width: 1024px) {
  .service_container {
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
  }
  .services {
    height: auto;
  }
}

/* Small Device */
@media screen and (max-width: 600px) {
  .service_container {
    grid-template-columns: 1fr;
    grid-gap: 1.5rem;
    gap: 1.5rem;
  }
}

.experience_container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 2rem;
  gap: 2rem;
}

.experience_container > div {
  background: var(--color-bg-variant);
  padding: 2.4rem 3rem;
  border: 1px solid transparent;
  border-radius: 2rem;
  transition: var(--transition);
}

.experience_container > div:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: defauld;
}

.experience_container > div h3 {
  text-align: center;
  margin-bottom: 2rem;
  color: var(--color-primary);
}

.experience_details > div small {
  color: var(--color-light);
}

.experience_content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-row-gap: 2rem;
  row-gap: 2rem;
}

.experience_details {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
}

.experience_detail-icon {
  display: flex;
  margin-top: 6px;
  color: var(--color-primary);
}

/* Medium Device */
@media screen and (max-width: 1024px) {
  .experience_container {
    grid-template-columns: 1fr;
  }

  .experience_container > div {
    width: 80%;
    padding: 2rem;
    margin: 0 auto;
  }

  .experience_content {
    padding: 1rem;
  }
}

/* Small Device */

@media screen and (max-width: 600px) {
  .experience_container {
    grid-gap: 1rem;
    gap: 1rem;
  }

  .experience_container > div {
    padding: 2rem 1rem;
  }

  .experience_details {
    margin-right: 1rem;
  }
}

.library_container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2.5rem;
  gap: 2.5rem;
  justify-content: center;
}

.library_item {
  background: var(--color-bg-variant);
  padding: 1.3rem;
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--transition);
}

.library_item:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
}

.library_img {
  overflow: hidden;
}

.library_img img {
  border-radius: 2rem;
  width: 100%;
}

.library_item h3 {
  margin: 1.2rem 0 2rem;
}

.library_item-cta {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 1rem;
}

/* Medium Device */
@media screen and (max-width: 1024px) {
  .library_container {
    grid-template-columns: 1fr 1fr;
    grid-gap: 1.2rem;
    gap: 1.2rem;
  }
  .library_img img {
    width: none;
  }
}

/* Small Device */
@media screen and (max-width: 600px) {
  .library_container {
    grid-template-columns: 1fr;
    grid-gap: 1rem;
    gap: 1rem;
  }
}

.container.coments_container {
  width: 40%;
  padding: 4rem;
}

.client_img {
  width: 4rem;
  border-radius: 4rem;
}
.client_avatar {
  width: 4rem;
  aspect-ratio: 1/1;
  overflow: hidden;
  border-radius: 50%;
  margin: 0 auto 1rem;
  border: 0.4rem solid var(--color-primary-variant);
}

.coments {
  background: var(--color-bg-variant);
  text-align: center;
  padding: 1.3rem;
  border-radius: 2rem;
  -webkit-user-select: none;
          user-select: none;
  margin-left: -2.5rem;
}

.client_coment {
  color: var(--color-light);
  font-weight: 300;
  display: block;
  width: 80%;
  margin: 0.8rem auto 0;
}

/* Medium Device */
@media screen and (max-width: 1024px) {
  .container.coments_container {
    width: 60%;
  }
}

/* Small Device */

@media screen and (max-width: 600px) {
  .container.coments_container {
    width: 50%;
  }
  .coments {
    padding: 1.5rem;
  }
}

.container.contact_container {
  width: 58%;
  display: grid;
  grid-template-columns: 30% 58%;
  grid-gap: 12%;
  gap: 12%;
}

.contact_options {
  display: flex;
  flex-direction: column;
  grid-gap: 1.2rem;
  gap: 1.2rem;
}

.contact_option {
  background: var(--color-bg-variant);
  width: 100%;
  padding: 1.2rem;
  border-radius: 1.2rem;
  text-align: center;
  border: 1px solid transparent;
  margin-bottom: 2rem;
  transition: var(--transition);
}

.contact_option:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
}

.contact_option-icon {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.contact_option a {
  margin-top: 0.7rem;
  display: inline-block;
  font-size: 0.8rem;
}

/* ===FORM=== */
form {
  display: flex;
  flex-direction: column;
  grid-gap: 1.2rem;
  gap: 1.2rem;
}

input,
textarea {
  width: 100%;
  background: transparent;
  padding: 1.5rem;
  border-radius: 0.5rem;
  border: 2px solid var(--color-primary-variant);
  color: var(--color-white);
  resize: none;
}

/* Medium Device */
@media screen and (max-width: 1024px) {
  .container.contact_container {
    grid-template-columns: 1fr;
    grid-gap: 1rem;
    gap: 1rem;
  }
}

/* Small Device */
@media screen and (max-width: 600px) {
  .container.contact_container {
    width: 77%;
    grid-gap: 0;
    gap: 0;
    margin-left: -0rem;
    margin: 1rem;
  }
}

footer {
  background: var(--color-primary);
  padding: 3rem 0;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;
}

footer a {
  color: var(--color-bg);
}

.footer_logo {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 2rem;
  display: inline-block;
}

.permalinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 5rem;
  gap: 5rem;
  margin: 0 auto 3rem;
}

.footer_socials {
  display: flex;
  justify-content: center;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 4rem;
}

.footer_socials a {
  background: var(--color-bg);
  color: var(--color-white);
  padding: 0.8rem;
  border-radius: 0.7rem;
  display: flex;
  border: 1px solid transparent;
}

.footer_socials a:hover {
  background: transparent;
  color: var(--color-bg);
  border-color: var(--color-bg);
}

.footer_copyright {
  margin-bottom: 4rem;
  color: var(--color-bg);
}

/* Small Device */
@media screen and (max-width: 600px) {
  .permalinks {
    flex-direction: column;
    grid-gap: 1.5rem;
    gap: 1.5rem;
  }
  .footer_socials {
    margin-bottom: 2.6rem;
  }
}

